.lista-tareas {
    max-width: 600px;
    min-width: 300px;
    margin: 2rem auto;
    background: #ffffff;
    border-radius: 16px;
    padding: 1.5rem 2rem;
    box-shadow: 0 8px 24px rgba(0,0,0,0.06);
}

.lista-tareas h2 {
    font-size: 1.25rem;
    font-weight: 600;
    color: #374151;
    margin-bottom: 1.2rem;
}